<template>
  <f7-app :params="{ theme: 'md', name: 'Mini-VSCO', id: 'com.mini.vsco' }">
     <f7-appbar>
       <div class="left">
         <h4 class="logo">Mini VSCO</h4>
       </div>
       <div class="right">
         <f7-button @click="open_new_image" class="display-flex" icon-f7='add_round' color='black'></f7-button>
         <f7-button id='download' @click='download' target='_blank' class="display-flex" :href="href" icon-f7='cloud_download' v-if="downloadable" color='black'></f7-button>
       </div>
       <form id='upload-form' action="#" enctype="multipart/form-data">
         <input @change='on_image_change' id="upload-input" type="file" accept="image/*" name="image" >
       </form>
     </f7-appbar>
     <f7-page id="main-page" infinite>
     <div id='img'>
       <canvas id="main-img" ></canvas>
     </div>
     <f7-block-title>iu chnh</f7-block-title>
     <f7-list accordion-list>
        <f7-list-item accordion-item title=" sng" >
          <f7-accordion-content>
            <f7-block>
              <f7-range ref="exposure" @range:changed="exposure_changed" :min="0" :max="100" :step="1" :label="false" :value="50" color="orange"></f7-range>
            </f7-block>
          </f7-accordion-content>
        </f7-list-item>
        <f7-list-item accordion-item title="Tng phn" >
          <f7-accordion-content>
            <f7-block>
              <f7-range ref="contrast" :min="0" @range:changed="contrast_changed" :max="50" :step="1" :label="false" :value="25" color="green"></f7-range>
            </f7-block>
          </f7-accordion-content>
        </f7-list-item>
        <f7-list-item  accordion-item title="Bo ha" >
          <f7-accordion-content>
            <f7-block>
              <f7-range ref="saturation" :min="0" :max="50" @range:changed="saturation_changed" :step="1" :label="false" :value="25" color="red"></f7-range>
            </f7-block>
          </f7-accordion-content>
        </f7-list-item>
        <f7-list-item accordion-item title="Nhiu" >
          <f7-accordion-content>
            <f7-block>
              <f7-range ref="noise" :min="0" :max="10"  @range:changed="noise_changed" :step="1" :label="false" :value="0" color="blue"></f7-range>
            </f7-block>
          </f7-accordion-content>
        </f7-list-item>
        <f7-list-item accordion-item title="Sc nt" >
          <f7-accordion-content>
            <f7-block>
              <f7-range ref="sharpen" :min="0" :max="100" @range:changed="sharpen_changed" :step="1" :label="false" :value="0" color="black"></f7-range>
            </f7-block>
          </f7-accordion-content>
        </f7-list-item>
     </f7-list>
     </f7-page>
  </f7-app>
</template>

<script>
/* eslint-disable */
import $ from 'jquery'
import Vue from 'vue'
import '@/assets/js/caman.full.min.js'
import _ from 'lodash'
export default {
  data () {
    return {
      downloadable: false,
      href: "#",
      image : null,
      specifications: {
        exposure: 0,
        contrast: 0,
        saturation: 0,
        sharpen: 0,
        noise: 0
      }
    }
  },
  name: 'App',
  methods: {
    download (e) {
      let this_ = this;
      Caman('#main-img',function(){
        this.save()
        this_.href = this.toImage('png')
        window.location = this_.href
      })
    },
    open_new_image () {
      $('#upload-input').trigger('click')
    },
    on_image_change (e) {
      let file = e.target.files[0]
      let reader  = new FileReader();
      reader.addEventListener("load",  () => {
        delete this.image
        let image = new Image()
        this.image = image
        image.src = reader.result   
        image.onload = () => {
          this.$refs.exposure.setValue(50)
          this.$refs.saturation.setValue(25)
          this.$refs.contrast.setValue(25)
          this.$refs.noise.setValue(0)
          this.$refs.sharpen.setValue(0)
          let canvas = document.getElementById('main-img')
          canvas.width = image.width
          canvas.height = image.height
          let ctx = canvas.getContext('2d')
          ctx.clearRect(0,0,canvas.width,canvas.height)
          ctx.imageSmoothingEnabled = true
          ctx.drawImage(image,0,0,canvas.width,canvas.height)
          $('#main-img').removeAttr('data-caman-id')
          this.downloadable = true
        } 
      }, false);
      reader.readAsDataURL(file)
    },
    draw_image(){
      $('#main-img').removeAttr('data-caman-id')
      let canvas = document.getElementById('main-img')
      canvas.width = this.image.width
      canvas.height = this.image.height
      let ctx = canvas.getContext('2d')
      ctx.clearRect(0,0,canvas.width,canvas.height)
      ctx.drawImage(this.image,0,0,canvas.width,canvas.height)
      let this_ = this
      Caman('#main-img',function(){
        this.exposure(this_.specifications.exposure)
        this.saturation(this_.specifications.saturation)
        this.contrast(this_.specifications.contrast)
        this.noise(this_.specifications.noise)
        this.sharpen(this_.specifications.sharpen)
        this.render()
      })
    },
    exposure_changed (e) {
      this.specifications.exposure =  e - 50
      this.draw_image()
    },
    saturation_changed (e) {
      this.specifications.saturation = e - 25
      this.draw_image()
    },
    contrast_changed (e) {
      this.specifications.contrast = e - 25
      this.draw_image()
    },
    noise_changed (e) {
      this.specifications.noise = e
      this.draw_image()
    },
    sharpen_changed (e) {
      this.specifications.sharpen = e
      this.draw_image()
    }
  }
}
</script>

<style lang="scss" scoped>
form#upload-form{
  display: none;
}
#main-page{
  margin-top: 2.5rem;
}
div#img{
  min-height: 87vw;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  #main-img{
    margin-top: 15px;
    max-width: 93%;
    min-width: 93%;
    box-sizing: border-box;
    border-style: solid;
    border-width: 3px;
    height: auto;
    border-color: #2c3e50;
    position: relative;
  }
}
</style>



